"use client"

import { useState, useEffect, useRef } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
// 에러 해결: PiggyBank, Wallet, TrendingDown 등 추가
import { 
  ChevronLeft, 
  Send, 
  Sparkles, 
  Bot, 
  Info, 
  User, 
  RefreshCw,
  Loader2,
  PiggyBank,
  Wallet,
  TrendingDown
} from "lucide-react"
import Image from "next/image"

export default function AIChatPage() {
  const router = useRouter()
  const [userName, setUserName] = useState("")
  const [messages, setMessages] = useState([
    { role: 'ai', content: '안녕하세요! NextMe AI 금융 비서입니다. 자산 분석이나 궁금한 점을 물어보세요.' }
  ])
  const [inputValue, setInputValue] = useState("")
  const [isTyping, setIsTyping] = useState(false)
  const scrollRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const storedName = localStorage.getItem("name")
    if (storedName) setUserName(storedName)
  }, [])

  // 메시지 전송 공통 로직
  const sendMessage = (text: string) => {
    if (!text.trim()) return

    const userMsg = { role: 'user', content: text }
    setMessages(prev => [...prev, userMsg])
    setInputValue("")
    setIsTyping(true)

    // AI 응답 시뮬레이션
    setTimeout(() => {
      setMessages(prev => [...prev, { 
        role: 'ai', 
        content: `'${text}'에 대해 분석한 결과, 최근 소비가 15% 감소하여 저축 여력이 충분해졌습니다. 추천 상품을 확인해 보시겠어요?` 
      }])
      setIsTyping(false)
    }, 1200)
  }

  const handleAISend = (e: React.FormEvent) => {
    e.preventDefault()
    sendMessage(inputValue)
  }

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }
  }, [messages, isTyping])

  return (
    <div className="min-h-screen bg-background text-foreground flex flex-col">
      {/* Header */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <Button variant="ghost" size="icon" onClick={() => router.back()}>
              <ChevronLeft className="h-5 w-5" />
            </Button>
            <Link href="/" className="flex items-center gap-2">
              <Image src="/logo.png" alt="Next Me" width={110} height={30} className="h-7 w-auto" />
            </Link>
          </div>
          <div className="flex items-center gap-2">
            <span className="text-xs font-black text-primary uppercase tracking-tighter">AI Advisor</span>
            <Avatar className="h-8 w-8 border">
              <AvatarFallback className="bg-primary/5 text-xs font-bold">{userName ? userName.charAt(0) : "U"}</AvatarFallback>
            </Avatar>
          </div>
        </div>
      </header>

      {/* Main Chat Area */}
      <main className="container mx-auto max-w-3xl flex-1 flex flex-col p-4 md:p-6 overflow-hidden">
        <div ref={scrollRef} className="flex-1 overflow-y-auto space-y-6 pr-2 mb-4 custom-scrollbar">
          {messages.map((msg, idx) => (
            <div key={idx} className={`flex ${msg.role === 'user' ? 'justify-end' : 'justify-start'} animate-in fade-in slide-in-from-bottom-2 duration-300`}>
              <div className={`flex gap-3 max-w-[85%] ${msg.role === 'user' ? 'flex-row-reverse' : ''}`}>
                <div className={`h-10 w-10 rounded-xl flex items-center justify-center shrink-0 border shadow-lg ${
                  msg.role === 'user' ? 'bg-primary border-primary shadow-primary/20' : 'bg-card border-border shadow-black/5'
                }`}>
                  {msg.role === 'user' ? <User className="h-5 w-5 text-primary-foreground" /> : <Sparkles className="h-5 w-5 text-primary" />}
                </div>
                <div className="flex flex-col gap-1.5">
                  <span className={`text-[10px] font-black uppercase tracking-widest text-muted-foreground/60 ${msg.role === 'user' ? 'text-right' : 'text-left'}`}>
                    {msg.role === 'user' ? userName : 'NextMe AI'}
                  </span>
                  <Card className={`p-4 rounded-2xl shadow-sm border-border/50 ${
                    msg.role === 'user' ? 'bg-primary text-primary-foreground rounded-tr-none border-none shadow-md' : 'bg-card/80 backdrop-blur-sm text-foreground rounded-tl-none'
                  }`}>
                    <p className="text-sm leading-relaxed font-medium">{msg.content}</p>
                  </Card>
                </div>
              </div>
            </div>
          ))}
          {isTyping && (
            <div className="flex justify-start gap-3 animate-pulse">
              <div className="h-10 w-10 rounded-xl bg-card border border-border flex items-center justify-center">
                <Loader2 className="h-5 w-5 text-primary animate-spin" />
              </div>
              <Card className="p-4 bg-card/50 rounded-2xl rounded-tl-none border-dashed">
                <div className="flex gap-1">
                  <div className="w-1.5 h-1.5 bg-primary rounded-full animate-bounce" />
                  <div className="w-1.5 h-1.5 bg-primary rounded-full animate-bounce [animation-delay:0.2s]" />
                  <div className="w-1.5 h-1.5 bg-primary rounded-full animate-bounce [animation-delay:0.4s]" />
                </div>
              </Card>
            </div>
          )}
        </div>

        {/* Suggestion Buttons */}
        {messages.length < 4 && !isTyping && (
          <div className="flex flex-wrap gap-2 mb-6 justify-center animate-in fade-in zoom-in-95 duration-500">
            <Button variant="outline" size="sm" className="rounded-full bg-card border-primary/20 hover:border-primary/50 font-bold text-xs group px-4 py-5" onClick={() => sendMessage("이번 달 내 지출 분석해줘")}>
              <TrendingDown className="h-3.5 w-3.5 mr-2 text-destructive group-hover:scale-110 transition-transform" />
              지출 분석
            </Button>
            <Button variant="outline" size="sm" className="rounded-full bg-card border-primary/20 hover:border-primary/50 font-bold text-xs group px-4 py-5" onClick={() => sendMessage("나에게 맞는 저축 상품 추천해줘")}>
              <PiggyBank className="h-3.5 w-3.5 mr-2 text-primary group-hover:scale-110 transition-transform" />
              저축 추천
            </Button>
            <Button variant="outline" size="sm" className="rounded-full bg-card border-primary/20 hover:border-primary/50 font-bold text-xs group px-4 py-5" onClick={() => sendMessage("내 자산 진단해줘")}>
              <Wallet className="h-3.5 w-3.5 mr-2 text-accent group-hover:scale-110 transition-transform" />
              자산 진단
            </Button>
          </div>
        )}

        {/* Input */}
        <div className="relative pt-2 pb-6">
          <form onSubmit={handleAISend} className="relative group">
            <div className="absolute -inset-1 bg-gradient-to-r from-primary/20 to-accent/20 rounded-2xl blur opacity-25 group-focus-within:opacity-100 transition duration-500" />
            <Input 
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              placeholder="AI에게 질문해보세요..." 
              className="relative w-full h-14 pl-6 pr-14 rounded-2xl border-border/50 bg-card shadow-2xl focus-visible:ring-primary text-base"
            />
            <Button type="submit" className="absolute right-2 top-2 h-10 w-10 rounded-xl shadow-lg transition-all active:scale-95" disabled={!inputValue.trim() || isTyping}>
              <Send className="h-5 w-5" />
            </Button>
          </form>
        </div>
      </main>
    </div>
  )
}